<!-- eslint-disable -->
<template>
  <div class="contacto-wrapper">
    <div id="contacto">
      <div class="left lateral">
        <h1 class="title main">Esperamos por si!</h1>
        <div class="line"></div>
        <div class="copy">
          <div class="topic">
            <div class="icon">
              <img :src="phone" alt />
            </div>
            <div class="txt">
              Fale connosco atrav√©s do
              <span id="tlf1">{{tlf1}}</span>
              <span id="tlf2">{{tlf2}}</span>
            </div>
          </div>

          <div class="topic">
            <div class="icon">
              <img :src="email" alt />
            </div>
            <div class="txt">
              <a style="color: #95989a" :href="'mailto:' + email1">{{email1}}</a>
              <a style="color: #95989a" :href="'mailto:' + email2">{{email2}}</a>
            </div>
          </div>

          <div class="topic">
            <div class="icon">
              <img :src="location" alt />
            </div>
            <div class="txt" v-html="address"></div>
          </div>
        </div>
        <div class="cta" @click="openContactForm()">
          <div class="btn blue">
            <div class="txt">contacte-nos</div>
          </div>
        </div>
      </div>
      <div class="right lateralR" v-html="api_code"></div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import phone from "../../assets/phone.png";
import email from "../../assets/email.png";
import location from "../../assets/location.png";
import axios from "axios";

export default {
  data() {
    return {
      phone: phone,
      email: email,
      location: location,
      tlf1: "",
      tlf2: "",
      email1: "",
      email2: "",
      address: "",
      api_code: "",
    };
  },
  methods: {
    openContactForm() {
      var contact_form = document.querySelector("#contact-form-2");
      var menu = document.querySelector("#menu");

      contact_form.style.transition = "1s";
      contact_form.style.transform = "translate(-50%, -50%)";
      contact_form.style.pointerEvents = "all";
      $("#contact-form-2 .page").css("pointer-events", "all");
      var html_elem = document.documentElement;
      html_elem.style.overflow = "hidden";

      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");

      $("#contact-form-2 .p0 .text").animate({ left: 0, opacity: 1 }, 500);
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getContactosHomepage.php")
      .then((resp) => {
        const data = resp.data;

        vm.tlf1 = data[0].tlf_1;
        if (data[0].tlf_2 == "") $("#tlf2").css("display", "none");
        else {
          $("#tlf2").css("display", "inline-block");
          vm.tlf2 = " ou " + data[0].tlf_2;
        }

        vm.email1 = data[0].email_1;
        if (data[0].email_2 == "") $("#email2").css("display", "none");
        else {
          $("#email2").css("display", "inline-block");
          vm.email2 = data[0].email_2;
        }

        vm.address = data[0].address;
        vm.api_code = data[0].google_maps_code;
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(65);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.contacto-wrapper {
  background-color: $lt-gray;
  height: pxToEm(540);
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #contacto {
    height: 100%;
    display: flex;
    margin: 0 auto;
    justify-content: space-around;
    align-items: center;
    max-width: pxToEm(1400);

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      flex-direction: column;
      margin: 0 pxToEm(20);
    }

    > div {
      flex: 1 1 auto;
      width: 40%;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        width: 100%;
      }
    }

    .left {
      // height: 70%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-right: pxToEm(10);

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        height: 100%;
        align-items: center;
      }

      .title {
        margin: 0;
        font-weight: normal;
        color: #95989a;
        margin-bottom: pxToEm(10);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(20);
        }
      }

      .line {
        width: pxToEm(79);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-bottom: pxToEm(30);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-bottom: pxToEm(30);
        }
      }

      .copy {
        font-size: pxToEm(20);
        color: #95989a;
        // height: pxToEm(120);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        font-family: "OS-Regular";
        margin-bottom: pxToEm(30);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          height: 100%;
        }

        .topic {
          display: flex;
          position: relative;
          margin-bottom: pxToEm(10);

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            margin-bottom: pxToEm(20);
          }

          .icon {
            width: pxToEm(31);
            height: pxToEm(31);
            display: flex;
            align-items: center;
            justify-content: center;
          }

          .txt {
            margin-left: pxToEm(5);

            p {
              margin: 0;
            }

            .link {
              color: #95989a;
            }
          }
        }
      }

      .cta {
        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-bottom: pxToEm(20);
        }

        .btn {
          width: pxToEm(202);
          height: pxToEm(67);
          border-radius: pxToEm(10);

          .txt {
            font-family: "OS-Regular";
            font-size: pxToEm(16);
            font-weight: bold;
            text-transform: uppercase;
          }
        }
      }
    }

    .right {
      margin-left: pxToEm(10);
      height: 60%;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin: 0;
        margin: pxToEm(40) 0;
      }
    }
  }
}
</style>



