<!-- eslint-disable -->
<template>
  <div class="especialidades-wrapper">
    <div id="especialidades">
      <div class="title-wrapper movement">
        <h1 class="title main">As Nossas Especialidades</h1>
        <div class="line"></div>
      </div>
      <div class="especialidades">
        <div class="especialidade movement" v-for="e in especialidades" :key="e.id">
          <div class="icon" :style="{'background-image': 'url(' + e.icon + ')'}"></div>
          <h3 class="txt-big">{{ e.title }}</h3>
          <div class="copy" v-html="e.content"></div>
          <router-link :to="e.url">
            <div class="cta">
              <div class="text">saiba mais</div>
              <div class="arrow-cont">
                <i class="arrow right"></i>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import FadeIn from "../../animations/fade";
import axios from "axios";

export default {
  data() {
    return {
      // FadeIn,
      especialidades: [
        {
          id: 1,
          title: "",
          code: "",
          content: "",
          url: "",
          icon: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get(
        "https://beta.anacarolinapereira.pt/db-api/getEspecialidadesHomepage.php"
      )
      .then((resp) => {
        const data = resp.data;
        var especialidades = [];

        map.call(data, function (d) {
          var src = d.img_src.split("uploads");
          especialidades.push({
            id: d.id,
            title: d.title,
            content: d.text,
            url: d.link,
            icon: "uploads" + src[1],
          });
        });

        vm.$data.especialidades = especialidades;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.especialidades-wrapper {
  height: pxToEm(650);
  height: auto;
  background-color: $lt-gray;
  padding: 0 pxToEm(230);
  overflow: hidden;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #especialidades {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: $mg-desktop;
    align-items: center;
    justify-content: space-between;

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      margin: 0 pxToEm(40);
    }

    .title-wrapper {
      align-items: center;
      margin-top: pxToEm(70);
      display: flex;
      flex-direction: column;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin-top: pxToEm(40);
      }

      .title {
        color: $dk-gray-3;
        margin: 0;
        font-weight: normal;
      }

      .line {
        width: pxToEm(79);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-top: pxToEm(15);
      }
    }

    .especialidades {
      margin-bottom: pxToEm(70);
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-top: pxToEm(40);
      flex-wrap: wrap;

      @media only screen and (max-width: 767px) {
        flex-wrap: wrap;
      }

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin-bottom: pxToEm(20);
        flex-wrap: wrap;
        flex-direction: column;
      }

      .especialidade {
        height: pxToEm(400);
        height: auto;
        // width: 25%;
        margin: 0 pxToEm(20);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin-bottom: pxToEm(70);

        @media only screen and (max-width: 767px) {
          margin-bottom: pxToEm(50);
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: 100%;
          margin: pxToEm(30) 0 pxToEm(25) 0;
          height: 100%;
        }

        .icon {
          width: pxToEm(138);
          height: pxToEm(138);
          background-color: #cbcbcb;
          border-radius: $circle;
          margin-bottom: pxToEm(30);
          background-size: cover;

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            width: pxToEm(108);
            height: pxToEm(108);
            margin-bottom: pxToEm(10);
          }
        }

        .txt-big {
          margin: 0;
          font-size: pxToEm(28);
          color: $dk-gray-3;
          text-align: center;
          margin-bottom: pxToEm(20);
          font-weight: normal;

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            font-size: pxToEm(20);
            margin-bottom: pxToEm(10);
          }
        }

        .copy {
          color: $dk-gray-3;
          max-width: pxToEm(376);
          width: 100%;
          font-size: pxToEm(16);
          text-align: center;
          font-family: "OS-Regular";
          min-height: pxToEm(170);
          margin-bottom: pxToEm(20);

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            min-height: pxToEm(110);
          }
        }

        .cta {
          color: $turquiose;
          font-size: pxToEm(14);
          text-transform: uppercase;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          width: pxToEm(131);
          height: pxToEm(44);
          border: pxToEm(1) solid $turquiose;
          border-radius: pxToEm(10);

          &:hover {
            .arrow-cont {
              transform: translateX(pxToEm(7));
            }
          }

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            margin-top: pxToEm(20);
          }

          .text {
            margin-top: pxToEm(2.5);
            margin-left: pxToEm(-6);
          }

          > div {
            position: relative;
            transition: 0.5s;

            .arrow {
              border: solid $turquiose;
              border-width: 0 pxToEm(1) pxToEm(1) 0;
              display: inline-block;
              padding: pxToEm(2);
              position: absolute;
              top: pxToEm(-2);
              left: pxToEm(4);

              &.right {
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
              }
            }
          }
        }
      }
    }
  }
}
</style>



