<!--eslint-disable-->
<template>
  <div class="formacoes-wrapper">
    <div id="formacoes">
      <div class="copy-content">
        <h1 class="title main movement">{{title}}</h1>
        <div class="line movement"></div>
        <div class="copy movement" v-html="text"></div>
      </div>
      <div class="cards-wrapper">
        <div class="card movement" v-for="f in formacoes" :key="f.id">
          <div class="icon" :style="{ 'background-image': 'url(' + f.icon + ')' }"></div>
          <div class="card-title">{{ f.title }}</div>
          <div class="card-copy">
            <i v-html="f.text"></i>
          </div>
          <router-link :to="f.url">
            <div class="cta">
              <div class="txt">saiba mais</div>
              <!-- <svg
                xmlns="http://www.w3.org/2000/svg"
                xmlns:xlink="http://www.w3.org/1999/xlink"
                viewBox="0 0 16 16"
              >
                <path
                  class="arrow"
                  d="M8,0,6.545,1.455l5.506,5.506H0V9.039H12.052L6.545,14.545,8,16l8-8Z"
                />
              </svg>-->
              <div class="arrow-cont">
                <i class="arrow right"></i>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      title: "",
      text: "",
      formacoes: [
        {
          id: "",
          title: "",
          text: "",
          url: "",
          icon: ''
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getFormacoesHomepage.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var intro = JSON.parse(result[1]);
        var forms = JSON.parse(result[2]);
        var formacoes = [];

        vm.title = intro[0].title;
        vm.text = intro[0].text;

        map.call(forms, function (f) {
        var src = f.icon.split("uploads");
          formacoes.push({
            id: f.id,
            title: f.title,
            text: f.text,
            url: f.link,
            icon: "uploads" + src[1]
          });
        });

        vm.$data.formacoes = formacoes;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.formacoes-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;
  overflow: hidden;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #formacoes {
    height: 100%;
    margin: $mg-desktop;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      margin: 0 pxToEm(20);
    }

    .copy-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      overflow: hidden;
      padding-bottom: pxToEm(40);

      .title {
        margin: 0;
        font-weight: normal;
        margin-top: pxToEm(60);
        color: $dk-gray-3;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(40);
        }
      }

      .line {
        width: pxToEm(79);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-top: pxToEm(15);
      }

      .copy {
        font-size: pxToEm(20);
        font-family: "OS-Regular";
        max-width: pxToEm(1000);
        text-align: center;
        width: 100%;
        margin-top: pxToEm(20);
        color: #707070;
      }
    }

    .cards-wrapper {
      display: flex;
      max-width: pxToEm(1400);
      width: 100%;
      justify-content: space-between;
      margin-bottom: pxToEm(80);
      height: 55%;
      flex-wrap: wrap;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        flex-direction: column;
        height: 100%;
        align-items: center;
        margin-bottom: pxToEm(40);
      }

      .card {
        width: pxToEm(301);
        height: pxToEm(431);
        background-color: $lt-gray;
        border-radius: pxToEm(23);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        padding: pxToEm(40) pxToEm(50);
        cursor: pointer;
        transition: 0.5s;
        margin: pxToEm(50) pxToEm(10) 0 pxToEm(10);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin: pxToEm(10) 0;
          width: pxToEm(301);
          height: pxToEm(331);
          padding: pxToEm(30) pxToEm(40);
        }

        &:hover {
          transform: scale(1.1);
          background-color: $turquiose;

          .card-title {
            color: $white;
          }

          .card-copy {
            color: $white;
          }

          a {
            align-self: flex-end;

            .cta {
              .txt {
                color: $white;
              }

              .arrow-cont {
                transform: translateX(pxToEm(10));

                .arrow {
                  border: solid #fff;
                  border-width: 0 pxToEm(1) pxToEm(1) 0;
                }
              }
            }
          }
        }

        .icon {
          width: pxToEm(80);
          height: pxToEm(80);
          border-radius: $circle;
          overflow: hidden;
          background-size: cover;
          background-color: #cbcbcb;
          background-position: center;

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            width: pxToEm(70);
            height: pxToEm(70);
          }
        }

        .card-title {
          font-family: Arial, Helvetica, sans-serif;
          font-size: pxToEm(22);
          color: $turquiose;
          font-weight: bold;
          text-align: center;
        }

        .card-copy {
          font-family: Georgia, "Times New Roman", Times, serif;
          font-size: pxToEm(20);
          color: #a4a4a4;
          text-align: center;
        }

        a {
          align-self: flex-end;

          .cta {
            width: pxToEm(80);
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-self: flex-end;
            align-items: center;

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              width: pxToEm(100);
              align-self: center;
            }

            .txt {
              font-family: "OS-Regular";
              color: #a4a4a4;
              text-transform: uppercase;
              font-size: pxToEm(14);

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                color: $turquiose;
              }
            }

            .arrow-cont {
              position: relative;
              transition: 0.5s;

              .arrow {
                border: solid #a4a4a4;
                border-width: 0 pxToEm(1) pxToEm(1) 0;
                display: inline-block;
                padding: pxToEm(2);
                position: absolute;
                top: pxToEm(-2);
                left: pxToEm(4);

                &.right {
                  transform: rotate(-45deg);
                  -webkit-transform: rotate(-45deg);
                }
              }
            }

            svg {
              width: pxToEm(11);
              height: pxToEm(11);
              overflow: visible;

              .arrow {
                fill: #a4a4a4;
                transition: 0.5s;

                @media only screen and (min-width: 300px) and (max-width: 599px) {
                  fill: $turquiose;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



