<!--eslint-disable-->
<template>
  <div class="parceiros-wrapper">
    <div id="parceiros">
      <div class="title main movement">Os Nossos Parceiros</div>

      <div class="pics-wrapper">
        <div
          class="pic movement"
          v-for="p in parceiros"
          :key="p.id"
          :style="{'background-image': 'url(' + p.img_src + ')'}"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      parceiros: [
        {
          id: "",
          name: "",
          img_src: "",
          url: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getParceirosHomepage.php")
      .then((resp) => {
        const data = resp.data;
        var parceiros = [];

        map.call(data, function (d) {
          var src = d.img_src.split("uploads");
          parceiros.push({
            id: d.id,
            name: d.name,
            img_src: "uploads" + src[1],
            url: d.link,
          });
        });

        vm.$data.parceiros = parceiros;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.parceiros-wrapper {
  background-color: $white;
  height: pxToEm(440);
  padding: $pd-desktop;
  height: auto;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #parceiros {
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: $mg-desktop;
    align-items: center;
    justify-content: space-between;

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      margin: 0 pxToEm(20);
    }

    .title {
      color: $dk-gray-3;
      margin: pxToEm(60) 0;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin: pxToEm(30) 0;
      }
    }

    .pics-wrapper {
      height: pxToEm(236);
      height: auto;
      display: flex;
      // margin-bottom: pxToEm(60);
      max-width: pxToEm(1440);
      width: 100%;
      justify-content: space-around;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        flex-wrap: wrap;
        height: auto;
      }

      .pic {
        margin: pxToEm(10);
        width: pxToEm(306);
        height: pxToEm(236);
        background-size: cover;
        background-repeat: no-repeat;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: pxToEm(206);
          height: pxToEm(136);
          margin: pxToEm(10);
          background-size: contain;
          background-position: center;
        }
      }
    }
  }
}
</style>



